import { createAction, createReducer } from "@reduxjs/toolkit";

const defaultState = {
    phone: '9179824659',
    email: 'dima18127@yandex.ru',
    nick: 'dima18127',
    name: 'Дмитрий',
    sername:'Макаров',
    sex: 'man',
    advantages: [{ value: "1", focus: "true" }],
    checkbox: ['2'],
    radiobox: '3',
    aboutInp: '',
  }
 export const addEmail = createAction('ADDEMAIL');
 export const addPhone = createAction('PHONE');
 export const setNick = createAction('NICK');
 export const setName = createAction('NAME');
 export const setSerName = createAction('SERNAME');
 export const setMail = createAction('MAIL');
 export const setADV = createAction('ADVANTAGE');
 export const setCheck = createAction('CHECKBOX');
 export const setRadio = createAction('RADIOBOX');
 export const setAbout = createAction('ABOUT');

export default createReducer (defaultState,{
[addEmail]:function (state) {return {...state, email:  addEmail.email}},
[addPhone]:function (state) {return {...state, phone:  addPhone.phone}},
[setNick]:function (state) {return {...state, nick:  setNick.nick}},
[setName]:function (state) {return {...state, name:  setName.name}},
[setSerName]:function (state) {return {...state, sername:  setSerName.sername}},
[setMail]:function (state) {return {...state, sex:  setMail.sex}},
[setADV]:function (state) {return {...state, advantages:  setADV.advantages}},
[setCheck]:function (state) {return {...state, checkbox:  setCheck.checkbox}},
[setRadio]:function (state) {return {...state, radiobox:  setRadio.radiobox}},
[setAbout]:function (state) {return {...state, aboutInp:  setRadio.aboutInp}},
})